.App {
    display: flex;
    flex-direction: column;
    align-content: center;
    margin: auto;
   
    height: 100%;
    background: linear-gradient(rgb(231, 231, 253), rgb(243, 236, 236));
}

.nasa-logo {
    margin: auto;
    padding: 5px;
    width: 200px;
    max-height: 250px;
}

.results-text {
    text-align: center;
}
@media screen and (max-width: 613px){
    .App {
        border: 1px solid;
            
    }
    .nasa-logo{
        width:150px;
    }
    
}